import { Obj, Mix } from "@kizmann/pico-js";

export default {

    name: 'NSwitch',

    inject: {

        NFormItem: {
            default: undefined
        }

    },

    props: {

        modelValue: {
            default()
            {
                return false;
            }
        },

        onValue: {
            default()
            {
                return true;
            }
        },

        offValue: {
            default()
            {
                return false;
            }
        },

        onType: {
            default()
            {
                return 'primary';
            }
        },

        offType: {
            default()
            {
                return 'default';
            }
        },

        size: {
            default()
            {
                return 'md';
            }
        },

        disabled: {
            default()
            {
                return false;
            },
            type: [Boolean]
        },

    },

    computed: {

        deepDisabled() {
            return this.NFormItem ? this.NFormItem.disabled(this.disabled) :
                this.disabled;
        }

    },

    data()
    {
        return {
            tempValue: this.modelValue
        };
    },

    methods: {

        eventClick()
        {
            let tempValue = this.onValue;

            if ( this.tempValue === this.onValue ) {
                tempValue = this.offValue;
            }

            this.$emit('update:modelValue', this.tempValue = tempValue);
        }

    },

    watch: {

        modelValue()
        {
            if ( this.modelValue !== this.tempValue ) {
                this.tempValue = this.modelValue;
            }
        }

    },

    renderSwitch()
    {
        let classList = [
            'n-switch__switch'
        ];

        let props = {};

        if ( ! this.deepDisabled ) {
            props.onMousedown = this.eventClick;
        }

        return (
            <div class={classList} {...props}>
                <span></span>
            </div>
        )
    },

    renderLabel()
    {
        if ( ! this.$slots.default ) {
            return null;
        }

        let classList = [
            'n-switch__label',
        ];

        let props = {};

        if ( ! this.deepDisabled ) {
            props.onMousedown = this.eventClick;
        }

        return (
            <div class={classList} {...props}>
                { this.$slots.default() }
            </div>
        )
    },

    render()
    {
        let classList = [
            'n-switch',
            'n-switch--' + this.size
        ];

        if ( this.tempValue === this.onValue ) {
            classList.push('n-switch--' + this.onType);
        }

        if ( this.tempValue === this.offValue) {
            classList.push('n-switch--' + this.offType);
        }

        if ( this.tempValue === this.onValue ) {
            classList.push('n-on');
        }

        if ( this.tempValue === this.offValue ) {
            classList.push('n-off');
        }

        if ( this.deepDisabled ) {
            classList.push('n-disabled');
        }

        return (
            <div class={classList}>
                { this.ctor('renderSwitch')() }
                { this.ctor('renderLabel')() }
            </div>
        );
    }

}
